















































import { Component, Vue, Prop } from 'vue-property-decorator'
import {
  BCard, BButton, BAvatar, BRow, BCol, BBadge,
} from 'bootstrap-vue'

interface IUser {
  id: string,
  name: string,
  avatar: string,
  address: string,
  address2: string,
  city: string,
  state: string,
  country: string,
  zipcode: string,
  email: string,
  phone: string[],
  fax: string[],
  isActive: boolean,
  ehrSystem: string,
  startDate: null,
  endDate: null,
  website: string,
}

@Component({
  name: 'HomeDetailUserCard',
  components: {
    BCard, BButton, BRow, BCol, BAvatar, BBadge,
  },
})
export default class HomeDetailUserCard extends Vue {
  @Prop({ required: true }) userData: IUser
}
